<template>
	<div id="mobile-app">
		<b-tabs pills end justified>
			<b-tab id="tab">
				<template #title>
					<b-icon icon="list"></b-icon>
				</template>
				<div id="main-mobile">
					<mobileMenuSingle/>
				</div>
			</b-tab>
			<b-tab id="tab">
				<template #title>
					<b-icon icon="archive"></b-icon>
					<b-badge 
						v-if="orderCount > 0"
						variant="danger"
					>{{orderCount}}</b-badge>
				</template>
				<div id="main-mobile">
					<orderSummary/>
				</div>
			</b-tab>
		</b-tabs>
	</div>
</template>

<script>
import dataManager from '../../dataManager';
import mobileMenuSingle from '@/views/menu/mobileMenuSingle.vue'
import orderSummary from '@/components/orderSummary.vue';
const convertStyle = () => {
	document.body.style.setProperty('--app-height', `${window.innerHeight}px`);
}


export default {
    data() {
		return {
			categories: []
		}
	},
	computed: {
		orderCount() {
			return this.$store.getters.orderItems.length;
		}
	},
	components: {
		orderSummary,
        mobileMenuSingle
	},
	mounted() {
		dataManager.getAllCategories()
			.then(c => this.categories = c);
		window.addEventListener("resize", convertStyle);
		window.addEventListener("DOMContentLoaded", convertStyle);
		convertStyle();
	}
}
</script>

