<template>
	<div id="order-tab-container">
		<div id="orders">
			<div v-for="item in orderItems" :key="item.line_no">
				<placeOrderCell 
					:operations="true" 
					:orderItem="item" 
					:infocodes="infocodes"
				></placeOrderCell>
			</div>
		</div>
		<div id="bottom">
			<div id="total">TOTAL: ${{total}}</div>
			<div id="confirm-btn-wrapper">
				<b-button id="confirm-btn" v-b-modal.confirmation>Confirm</b-button>	
			</div>
		</div>
		<b-modal 
			id="confirmation"
			title="Place Order"
			@ok="confirmOrder"
		>
			<p class="my04">Do you want to place the order?</p>
		</b-modal>
	</div>
</template>

<script>
import placeOrderCell from '@/components/placeOrderCell';
export default {
	components: {
		placeOrderCell
	},
	computed: {
		orderItems() {
			return this.$store.getters.orderItems;
		},
		infocodes() {
			return this.$store.getters.infocodes;
		},
		total() {
			let t = 0;
			this.$store.getters.orderItems.map(line => {
				let p  = line.unit_price
				let infocodes = this.$store.getters.infocodes.filter(x => x.item_line_no === line.line_no);
				infocodes.map(infocode => {
					if (infocode.subcode_object.amount) {
						p += infocode.subcode_object.amount;
					}
				})
				t += line.quantity * p;
			});
			return parseFloat(t).toFixed(2);
		},
	},
	methods: {
		confirmOrder() {
			this.$store.dispatch('addTableOrder');
		},
	},
};
</script>

