<template>
	<div id="order-summary-container">
		<div id="summary-header">
			<div>Table No.: {{tableNo}}</div>
			<div>Entry Time: {{entryTime}}</div>
		</div>
		<b-tabs nav-wrapper-class="sticky-top bg-light" fill>
			<b-tab>
				<template #title>
						Cart
						<b-badge 
							v-if="orderCount > 0"
							variant="danger"
					>{{orderCount}}</b-badge>
				</template>
					<placeOrderTab></placeOrderTab>	
			</b-tab>
			<b-tab>
				<template #title>
					Orders
					<b-badge 
						v-if="tableOrderCount > 0"
						variant="secondary"
					>{{tableOrderCount}}</b-badge>
				</template>
				<orderStatusTab></orderStatusTab>
			</b-tab>
		</b-tabs>
	</div>
</template>

<script>
import placeOrderTab from '@/components/placeOrderTab';
import orderStatusTab from '@/components/orderStatusTab';
export default {
	components: {
		placeOrderTab,
		orderStatusTab
	},
	computed: {
		tableNo() {
			return this.$store.getters.tableNo;
		},
		entryTime() {
			if (this.$store.getters.entryTime) {
				return this.$store.getters.entryTime.format('DD/MM/yy hh:mm');
			}
			return '';
		},
		orderCount() {
			return this.$store.getters.orderItems.length;
		},
		tableOrderCount(){
			return this.$store.getters.tableOrderItems.length;
		}
	}
};
</script>

