<template>
    <div id="infocodePanel">
        <div
            v-for="code in itemInfocodes"
            :key="code.infocode_code"
        >
            <infocodeCell
                :itemInfoCode="code"
                @subcodeChange="subcodeChange"
            />
        </div>
    </div>
</template>

<script>
import dataManager from '../../dataManager';
import infocodeCell from '@/components/info-codes/infocodeCell';
export default {
    props: {
        item_no: {
            type: String,
            default() {
                return '';
            }
        }
    },
    components: {
        infocodeCell
    },
    data() {
        return {
            itemInfocodes: [],
            // selectedSubcodes: [],
        }  
    },
    methods: {
        subcodeChange(sc) {
            this.$emit('subcodeChange', sc);
        }
    },
    async mounted() {
        this.itemInfocodes =await dataManager.geItemInfocodes(this.item_no);
    }
}
</script>

