<template>
	<div id="status-tab-container">
		<!-- <div id="orderNo">
			<div>{{orderNo}}</div>
		</div> -->
		<div id="orders">
			<div v-for="item in orderItems" :key="item.line_no">
				<placeOrderCell 
					:orderItem="item" 
					:infocodes="infocodes"
				></placeOrderCell>
			</div>
		</div>
		<div id="bottom">
			<div id="total">TOTAL: ${{total}}</div>
		</div>
	</div>
</template>

<script>
import placeOrderCell from '@/components/placeOrderCell';
export default {
	components: {
		placeOrderCell,
	},
	computed: {
		orderItems() {
			return this.$store.getters.tableOrderItems;
		},
		infocodes() {
			return this.$store.getters.tableOrderInfocodes;
		},
		total() {
			let t = 0;
			this.$store.getters.tableOrderItems.map(line => {
				let p  = line.unit_price
				let infocodes = this.infocodes.filter(x => x.item_line_no === line.line_no);
				infocodes.map(infocode => {
					if (infocode.subcode_object.amount) {
						p += infocode.subcode_object.amount;
					}
				})
				t += line.quantity * p;
			});
			return parseFloat(t).toFixed(2);
		},
		orderNo() {
			if (this.$store.getters.orderNo) {
				return `Order No. ${this.$store.getters.orderNo}`;
			}
			return '';
		}
	},
	methods: {
		confirmOrder() {
			this.$store.dispatch('addKitchenOrder');
		},
	}
};
</script>

