<template>
    <div>
		<div id="banner">
			<img id="banner-img" src="@/assets/20200320_SY-website-banner-01-3-scaled.jpg" alt="">
		</div>
		<b-nav 
			b-card-header 
			id="header" 
			slot="header" 
			pills
			v-b-scrollspy:nav-scroller
			variant="secondary"
		>
			<b-nav-item
				exact exact-active-class="active"
				v-for="cat in categories"
				:key="cat.id"
				:href="'#' +cat.id"
				@click="scrollIntoView"
			> 
				<div id="tab-content"  :href="'#' +cat.id" @click="$emit('click')">
					<div id="tab-icon-wrapper" :href="'#' +cat.id" @click="$emit('click')">
						<img id="tab-icon" :src="`${cat.icon}`" alt="" :href="'#' +cat.id" @click="$emit('click')">
					</div>
					{{ cat.description }}
				</div>
			</b-nav-item>
		</b-nav>
		<div id="nav-scroller" ref="content">
			<div v-for="cat in categories" :key="cat.id">
				<div id="nav-scroller-header">
					<div :id="cat.id" class="text">{{cat.description}}</div>
				</div>
				<div v-for="item in items[cat.id]" 
					:key="item.item_no"
					@click="handleItemClick(item)"
				>
					<itemCell 
						:item="item"
					></itemCell>
				</div>
			</div>
		</div>
		<b-modal id="detail" ref="detail"
			:title="selectedItem.description"
			centered
			hide-footer
		>
			<itemDetail
				:item="selectedItem"
				@close="closeDetail"
			/>
		</b-modal>
	</div>
</template>

<script>
import dataManager from '../../dataManager';
import itemCell from '@/components/itemCellMobile.vue';
import itemDetail from '@/components/itemDetail';
const convertStyle = () => {
	document.body.style.setProperty('--app-height', `${window.innerHeight}px`);
}


export default {
    data() {
		return {
			categories: [],
			items: {},
			selectedItem: {},
			catIcons: []
		}
	},
	components: {
		// orderSummary,
		itemCell,
		itemDetail
	},
	methods: {
		test() {
			console.log('test');
		},
		headerScrollIntoView(target) {
			const el = document.querySelector(`[href*="${target}"]`);
			if (el) {
				el.scrollIntoView();
			}
		},
		scrollIntoView(event) {
			event.preventDefault();
			const href = event.target.getAttribute('href');
			const el = href ? document.querySelector(href) : null;
			if (el) {
				this.$refs.content.scrollTop = el.offsetTop;
			}
		},
		async handleItemClick(item) {
			let infocode = await dataManager.geItemInfocodes(item.item_no);
			if (infocode.length > 0) {
				this.showDetail(item);
			} else {
				let itemDetail  = {
					qty: 1,
					item_no: item.item_no
				}
				this.$store.dispatch('addToOrderWithDetail', itemDetail);
			}
		},
		showDetail(item) {
			this.selectedItem = item;
			this.$refs['detail'].show();
		},
		closeDetail() {
			this.$refs['detail'].hide();
		},
		// async loadImages() {
		// 	this.categories.map(cat => {
		// 		let image = require(cat.icon);;
		// 		this.catIcons.push({id: cat.id, image: image});
		// 	});
		// },
		// getCatIcons(id) {
		// 	let iconObj = this.catIcons.find(x => x.id === id);
		// 	return iconObj.image
		// }
	},
	async mounted() {
		await dataManager.init();
		dataManager.getAllCategories()
			.then(c => this.categories = c);
		window.addEventListener("resize", convertStyle);
		window.addEventListener("DOMContentLoaded", convertStyle);
		convertStyle();
		this.items = await  dataManager.allItemsByCat();
		// await this.loadImages();
	},
	created() {
		this.$root.$on('bv::scrollspy::activate', this.headerScrollIntoView);
	}
}
</script>

