<template>
    <div id="infocodeCell">
       <h6>{{infoCodeObj.infocode.description}}</h6>
        <div
            id="infocode-body"
        >
            <div>
                <b-button
                    id="button"
                    v-for="(cs, idx) in infoCodeObj.subcodeStates"
                    :key="idx"
                    :pressed.sync="cs.state"
                    @click="handleStateChange(idx)"
                >
                    {{cs.subcode.description}} 
                    {{amountStr(cs.subcode)}}
                </b-button>
            </div>
        </div>
    </div>
</template>

<script>
import dataManager from '../../dataManager';
export default {
    props: {
        itemInfoCode: {
            type: Object,
            default() {
                return {};
            }
        },
    },
    data() {
        return {
            subcodes: [],
            // codeStates: [],
            infoCodeObj: {
                infocode: {},
                subcodeStates: []
            },    
        }  
    },
    methods: {
        amountStr(subcode) {
            let str = '';
            if (subcode.amount && subcode.amount > 0) {
                str += '$' + subcode.amount;
            }
            return str;
        },
        handleStateChange(i) {
            if (this.infoCodeObj.infocode.required) {
                this.infoCodeObj.subcodeStates.forEach((b, index) => b.state = i === index);
            }
            this.$emit('subcodeChange', this.infoCodeObj);
        }
    },
    components: {
    },
    async mounted() {
        let infoCode =  await dataManager.getInfoCode(this.itemInfoCode.infocode_code);
        this.infoCodeObj.infocode = infoCode;
        let subcodes = await dataManager.getInfoSubcodes(this.itemInfoCode.infocode_code);
        subcodes.map((sc,idx) => {
            let subcodeState = { subcode: sc, state: false}
            if (idx ==0 && infoCode.required) {
                subcodeState.state = true;
            }
            this.infoCodeObj.subcodeStates.push(subcodeState);
        });
        this.$emit('subcodeChange', this.infoCodeObj);
    }
}
</script>

